.feature {
     &__card {
          background: hsl(var(--white));
          box-shadow: 0px 0px 35px 0px hsl(var(--black) / .05);
          border-radius: 10px;
          padding: 30px;
          height: 100%;
          margin: auto;
          position: relative;
          overflow: hidden;
          transition: .3s;
          z-index: 2;
          &::before {
               content: "";
               position: absolute;
               bottom: -30px;
               left: -30px;
               width: 100px;
               height: 100px;
               border: 5px solid hsl(var(--base-two-d-300) / .1);
               border-radius: 50%;
               transition: .3s;
               z-index: -1;
          }
          @include lg-screen {
               padding: 30px;
          }
          @include sm-screen {
               max-width: 320px;
               padding: 20px;
          }
          &__icon {
               width: 70px;
               height: 70px;
               display: flex;
               justify-content: center;
               align-items: center;
               margin: 0 auto 20px;
               @include md-screen {
                    width: 50px;
                    height: 50px;
                    margin-bottom: 15px;
               }
               img {
                    max-width: 100%;
                    max-height: 100%;
               }
          }
          &__title {
               text-align: center;
               @include fs-20;
               line-height: 1;
               font-weight: 600;
               color: hsl(var(--black) / .8);
               margin-bottom: 0;
               @include lg-screen {
                    @include fs-18;
               }
               @include xsm-screen {
                    @include fs-16;
               }
          }
          &:hover {
               transform: translateY(-10px);
               &::before {
                    transform: scale(1.2);
               }
          }
     }
}