.buy-sell-crypto {
     position: relative;
     z-index: 2;
     &__vector-1 {
          position: absolute;
          top: 100px;
          right: 50px;
          width: 100px;
          filter: drop-shadow(0px 30px 25px hsl(var(--black) / .4));
          animation: pulse 3s linear infinite;
          opacity: 0.7;
          z-index: -1;
          @include lg-screen {
               top: 80px;
               right: 30px;
               width: 65px;
               filter: drop-shadow(0px 20px 20px hsl(var(--black) / .4));
          }
          @include md-screen {
               top: 30px;
               right: 20px;
               width: 40px;
               filter: drop-shadow(0px 10px 10px hsl(var(--black) / .4));
          }
     }
     &__vector-2 {
          position: absolute;
          bottom: 100px;
          left: 50px;
          width: 100px;
          filter: drop-shadow(0px 50px 20px hsl(var(--black) / .2));
          animation: pulse 3s linear infinite;
          opacity: 0.7;
          z-index: -1;
          @include lg-screen {
               bottom: 80px;
               left: 30px;
               width: 65px;
               filter: drop-shadow(0px 20px 25px hsl(var(--black) / .4));
          }
          @include md-screen {
               bottom: 30px;
               left: 20px;
               width: 30px;
               filter: drop-shadow(0px 10px 10px hsl(var(--black) / .4));
          }
     }
     .tab-content {
          position: relative;
     }
     &__float-btn {
          position: absolute;
          bottom: calc(100% + 41px);
          right: 0;
          @include md-screen {
               bottom: calc(100% + 26px);
          }
          .dropdown-toggle {
               height: 42px;
               padding: 0 25px;
               @include md-screen {
                    height: 33px;
                    padding: 0 10px;
               }
          }
     }
}