/* ====================================== Alert Css Start =============================== */
.alert {
    margin-bottom: 0;
    background-color: hsl(var(--white));
    font-weight: 400;
    padding: 10px 15px;
    border-radius: 5px;
    @include md-screen {
        padding: 10px 15px;
    }
    @include msm-screen {
        padding: 10px 15px;
    }
    &__icon {
        @include fs-18;
        line-height: 1;
    }
    &__content {
        width: calc(100% - 24px);
        padding-left: 10px;
    }
    &__title {
        @extend .fs-16; 
        color: hsl(var(--base-two)/.8);
        font-weight: 600;
        font-family: var(--heading-font); 
        margin-bottom: 6px;
    }
    &__desc {
        color: hsl(var(--base-two)/.5);
        display: block;
        line-height: 1.375;
        @include xsm-screen {
            @include fs-13; 
        }
    }
    &__link {
        position: relative;
        &:hover {
            &::before {
                visibility: visible;
                opacity: 1;
                bottom: 0;
            }
        }
        &::before {
            @extend .pa-extend; 
            left: 0;
            bottom: -5px;
            width: 100%;
            height: 1px;
            background-color: hsl(var(--base));
            visibility: hidden;
            opacity: 0;
            transition: .2s ease-in-out;
        }
    }

    &--base {
        border-color: hsl(var(--base) / .15);
        background: hsl(var(--base) / .05);
        .alert__icon {
            color: hsl(var(--base));
        }
    }
    &--primary {
        border-color: hsl(var(--primary)/.15);
        background: hsl(var(--primary) / .05);
        .alert__icon {
            color: hsl(var(--primary));
        }
    }
    &--success {
        border-color: hsl(var(--success)/.15);
        background: hsl(var(--success) / .05);
        .alert__icon {
            color: hsl(var(--success));
        }
    }
    &--info {
        border-color: hsl(var(--info)/.15);
        background: hsl(var(--info) / .05);
        .alert__icon {
            color: hsl(var(--info));
        }
    }
    &--danger {
        border-color: hsl(var(--danger)/.15);
        background: hsl(var(--danger) / .05);
        .alert__icon {
            color: hsl(var(--danger));
        }
    }
    &--warning {
        border-color: hsl(var(--warning)/.15);
        background: hsl(var(--warning) / .05);
        .alert__icon {
            color: hsl(var(--warning));
        }
    }
    &--secondary {
        border-color: hsl(var(--secondary)/.15);
        background: hsl(var(--secondary) / .05);
        .alert__icon {
            color: hsl(var(--secondary));
        }
    }
    &--dark {
        border-color: hsl(var(--dark)/.15);
        background: hsl(var(--dark) / .05);
        .alert__icon {
            color: hsl(var(--dark));
        }
    }
}
/* ====================================== Alert Css End =============================== */