/* ================================= Button Css Start =========================== */
.btn-check:checked+.btn, .btn.active, .btn.show, .btn:first-child:active, :not(.btn-check)+.btn:active {
    color: none;
    background-color: none;
    border-color: none;
}

.pill {
    border-radius: 40px !important;
}

.btn {
    color: hsl(var(--white)) !important;
    font-weight: 500;
    padding: 8px 20px;
    border-radius: 25px;
    position: relative;
    z-index: 1;
    border: 1px solid transparent;
    font-family: var(--body-font);
    @include fs-16;
    @include sm-screen {
        padding: 6px 20px;
        @include fs-14;
    }
    @include xsm-screen {
        padding: 5px 20px;
    }
    &:hover, &:focus, &:focus-visible, &.active {
        box-shadow: none !important;
    }
    &--lg {
        padding: 10px 35px;
        @include md-screen {
            padding: 9px 30px;
        }
        @include sm-screen {
            padding: 8px 25px;
        }
    }
    &--sm {
        padding: 3px 12px;
        @include fs-14;
        @include sm-screen {
            @include fs-13;
            padding: 3px 10px;
        }
    }
    &--icon {
        width: 35px;
        height: 35px;
        @extend .fs-15; 
        padding: 0;
        display: inline-flex;
        justify-content: center;
        align-items: center;
    }
    .icon {
        margin-right: 5px;
    }
    // ============= Different Color Button Start ==================
    &--base {
        background-color: hsl(var(--base)) !important;
        border: 1px solid hsl(var(--base)) !important;
        &:hover, &:focus, &:focus-visible, &.active {
            background-color: hsl(var(--base-d-100)) !important;
            border-color: hsl(var(--base-d-100)) !important;
        }
        &__2 {
            background-color: hsl(var(--base-two)) !important;
            border: 1px solid hsl(var(--base-two)) !important;
            &:hover, &:focus, &:focus-visible, &.active {
                background-color: hsl(var(--base-two-d-200)) !important;
                border-color: hsl(var(--base-two-d-200)) !important;
            }
        }
    }
    &-outline--base {
        background-color: transparent !important;
        border: 1px solid hsl(var(--base) / .3) !important;
        color: hsl(var(--base)) !important;
        &:hover, &:focus, &:focus-visible, &.active {
            background-color: hsl(var(--base)) !important;
            border-color: hsl(var(--base)) !important;
            color: hsl(var(--white)) !important;
        }
    }
    &--primary {
        background-color: hsl(var(--primary)) !important;
        &:hover, &:focus, &:focus-visible, &.active {
