/* ================================= Pagination Css Start =========================== */
.pagination {
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 36px;
    @include lg-screen {
        margin-top: 28px;
    }
    @include md-screen {
        margin-top: 20px;
    }
    @include sm-screen {
        margin-top: 12px;
    }
    .page-item {
        &.active {
            .page-link {
                background-color: hsl(var(--base));
                color: hsl(var(--base-two-l-500));
                border-color: hsl(var(--base));
            }
        }
        .page-link {
            background: hsl(var(--white) / .1);
            border-color: hsl(var(--base-two) / .3);
            margin: 0 5px;
            border-radius: 5px;
            height: 40px;
            min-width: 40px;
            display: flex;
            justify-content: center;
            align-items: center;
            font-weight: 500;
            padding: 0;
            color: hsl(var(--base-two-d-300));
            &:hover {
                background-color: hsl(var(--base));
                color: hsl(var(--white));
                border-color: hsl(var(--base));
            }
            &:focus {
                box-shadow: none;
            }
        }
    }
}
/* ================================= Pagination Css End =========================== */