/* ================= Slick Arrow & Dots css Start ================ */ 
.slick-initialized.slick-slider {
  .slick-list {
    margin: 0 -15px;
    @include sm-screen {
      margin: 0;
    }
  }
  .slick-slide {
    height: auto;
    padding: 0 15px;
    > div {
      height: 100%;
    }
  }
}


.slick-arrow {
  border: none;
  width: 50px;
  height: 50px;
  line-height: 50px;
  background: hsl(var(--base));
  border: 1px solid hsl(var(--base));
  border-radius: 10px;
  color: hsl(var(--white));
  display: flex;
  justify-content: center;
  align-items: center;
  transition: .4s;
  @include fs-35;
  position: relative;
  z-index: 2;
}
.slick-arrow:hover {
  background: hsl(var(--white));
  border-color: hsl(var(--base) / .4);
  color: hsl(var(--base));
}
.slick-prev {
  background: hsl(var(--base-two));
  border: 1px solid hsl(var(--base-two));
  &:hover {
    border-color: hsl(var(--base-two) / .4);
    color: hsl(var(--base-two));
  }
}
/* Dots Css Start */ 
.slick-dots {
  text-align: center;
  padding-top: 20px;
  display: flex;
  justify-content: center;
  li {
    display: inline-block;
    height: 8px;
    button {
      border: none;
      width: 40px;
      height: 8px;
      text-indent: -9999px;
      transition: .3s linear;
      position: relative;
      &::after {
        content: "";
        position: absolute;
        top: 50%;
        left: 0;
        right: 0;
        height: 1px;
        background: hsl(var(--black) / .3);
        transform: translateY(-50%);
        transition: .3s linear;
      }
    }
    &.slick-active button {
      background-color: hsl(var(--base));
      &::after {
        background: transparent;
      }
    }
  }
}
/* Dots Css End */
