/* ================================= Common Typography Css Start =========================== */
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body {
    font-family: var(--body-font);
    color: hsl(var(--body-color));
    word-break: break-word;
    min-height: 100vh;
    @include fs-17;
    line-height: 1.6;
}
span {
  display: inline-block;
}

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin: 0 0 20px 0;
    font-family: var(--heading-font);
    color: hsl(var(--heading-color));
    line-height: 1.3;
    font-weight: 700;
    @include sm-screen {
      margin: 0 0 15px 0;
    }
  }
  h1 {
    font-size: clamp(1.75rem, 0.1327rem + 3.3694vw, 3.5rem); // Min:28px - Max:56px
  }
  h2 {
    font-size: clamp(1.5rem, 0.3542rem + 2.6474vw, 3rem); // Min: 24px - Max: 48px
  }
  h3 {
    font-size: clamp(1.375rem, 0.3353rem + 2.1661vw, 2.5rem); // Min: 22px - Max: 40px
  }
  h4 {
    font-size: clamp(1.25rem, 0.5569rem + 1.444vw, 2rem); // Min: 20px - Max: 32px
  }
  h5 {
    font-size: clamp(1.125rem, 0.7784rem + 0.722vw, 1.5rem); // Min: 18px - Max: 24px
  }
  h6 {
    font-size: clamp(1rem, 0.769rem + 0.4813vw, 1.25rem); // Min: 16 - Max-20
  }
  
  h1 > a,
  h2 > a,
  h3 > a,
  h4 > a,
  h5 > a,
  h6 > a {
    font-weight: inherit;
    font-size: inherit;
    color: inherit;
    transition: .2s linear;
    line-height: 1.3;
  }

a {
    display: inline-block;
    transition: .2s linear;
    text-decoration: none;
    color: hsl(var(--base-l-100));
    line-height: inherit;
}
a:hover {
  color: hsl(var(--base-d-100));
}
img {
    max-width: 100%;
    height: auto;
}
select {
  cursor: pointer;
}
ul,
ol {
  padding: 0;
  margin: 0;
  list-style: none;
}
code {
  font-size: inherit;
  font-weight: 600;
}
button {
  border: 0;
  background-color: transparent;
  &:focus {
    outline: none;
    box-shadow: none;
  }
}
textarea {
  vertical-align: middle;
}
i {
  display: inline-block;
}
.form-select:focus {
  outline: 0;
  box-shadow: none;
}
.list-styled {
  margin-bottom: 1rem;
  li {
    padding-left: 1.5rem;
    margin-bottom: 10px;
    position: relative;
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: -6px;
      transform: rotate(-45deg) translateY(12px);
      width: 10px;
      height: 10px;
      border-style: double;
      border-color: hsl(var(--base));
      border-width: 6px;
      border-right-width: 1px;
      border-bottom-width: 1px;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
}
/* ================================= Common Typography Css End =========================== */