/* ====================== Section Heading Start ==================== */
.section-heading {
  position: relative;
  margin-bottom: 58px;
  @include lg-screen {
    margin-bottom: 40px;
  }
  @include md-screen {
    margin-bottom: 30px;
  }
  &-light {
    color: hsl(var(--white));
  }
  &__subtitle {
    @include fs-20;
    line-height: 1;
    font-weight: 500;
    color: hsl(var(--base));
    margin-bottom: 10px;
    @include md-screen {
      @include fs-18;
    }
  }
  &__title {
    @include fs-50;
    line-height: 1.2;
    position: relative;
    color: hsl(var(--black) / .8);
    @include lg-screen {
      @include fs-40;
    }
    @include md-screen {
      @include fs-30;
      margin-bottom: 0;
    }
  }
  // Style left
  &.style-left {
    text-align: left;
    .section-heading {
      &__title {
        &::before {
          left: 0;
          transform: translateX(0);
        }
      }
      &__desc {
        margin-left: 0;
      }
    }
  }
}
/* ====================== Section Heading End==================== */


/* ====================== Simple Date Picker jQuery Plugin Start ==================== */
//Plugin URL: https://www.jqueryscript.net/time-clock/Highly-Customizable-jQuery-Datepicker-Plugin-Datepicker.html
.datepicker-container {
  width: 300px;
  line-height: calc(300px / 7);
}
.datepicker-dropdown {
  border: 0;
  border-top: 1px solid hsl(var(--black) / .1);
  box-shadow: 0px 1px 1px 1px hsl(var(--black) / .05);
  border-radius: 5px;
}
.datepicker-panel>ul {
  width: 100%;
  &:first-child {
    border-bottom: 1px solid hsl(var(--black) / .05);
  }
  &:not(:first-child)>li {
    width: calc(100% / 7);
    height: auto;
    aspect-ratio: 1 / 1;
    display: flex;
    align-items: center;
    justify-content: center;
    @include fs-14;
    border-radius: 3px;
  }
  >li {
    &[data-view="month next"],
    &[data-view="month prev"],
    &[data-view="year next"],
    &[data-view="year prev"],
    &[data-view="years next"],
    &[data-view="years prev"],
    &[data-view=next] {
      width: calc(100% / 7);
      height: auto;
      aspect-ratio: 1 / 1;
      display: flex;
      justify-content: center;
      align-items: center;
      @include fs-14;
      border-radius: 3px;
    }
    &[data-view="month current"],
    &[data-view="year current"],
    &[data-view="years current"] {
      width: calc(100% / 7 * 5);
      height: auto;
      display: flex;
      justify-content: center;
      align-items: center;
      @include fs-15;
      color: hsl(var(--base));
      font-weight: 600;
      border-radius: 3px;
    }
    &.muted,
    &.muted:hover {
      color: hsl(var(--black) / .4);
    }
    &.picked,
    &.picked:hover {
      color: hsl(var(--base));
      font-weight: 600;
    }
    &.highlighted {
      background: hsl(var(--base-l-900));
      font-weight: 600;
      &:hover {
        background: hsl(var(--base-l-800));
      }
    }
    &:hover {
      background: hsl(var(--base-l-900));
    }
  }
  &[data-view=months]>li,
  &[data-view=years]>li {
    width: calc(100% / 3);
    height: 50px;
  }
  &[data-view=week]>li {
    font-weight: 600;
  }
}
.datepicker-top-left, .datepicker-top-right {
  border-top-color: hsl(var(--base));
  &::before {
    border-bottom-color: hsl(var(--base));
  }
}
/* ====================== Simple Date Picker jQuery Plugin End ==================== */


/* ====================== Custom Image Upload with Preview Start ==================== */
.upload__img {
  width: max-content;
  margin: auto;
  position: relative;
  &__btn {
       position: absolute;
       bottom: 0;
       right: 0;
       width: 40px;
       height: 40px;
       background: hsl(var(--base));
       color: hsl(var(--white));
       @include fs-24;
       display: flex;
       align-items: center;
       justify-content: center;
       border-radius: 50%;
       transform: translate(10%, 10%);
       cursor: pointer;
       transition: .3s;
       z-index: 2;
       &:hover {
            background: hsl(var(--base-d-200));
       }
  }
  input {
    position: absolute;
    opacity: 0;
    bottom: 0;
    left: 0;
    width: 180px;
    height: 180px;
    cursor: pointer;
    z-index: 1;
    &:hover + .upload__img-preview {
      background: hsl(var(--base-l-800));
      border-color: hsl(var(--base));
    }
  }
}
.upload__img-preview {
  width: 180px;
  height: 180px;
  background: hsl(var(--base-l-900));
  border: 1px dashed hsl(var(--black) / .1);
  border-radius: 5px;
  padding: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: var(--heading-font);
  @include fs-80;
  font-weight: 100;
  transition: .3s;
  img {
    width: 100%;
    height: 100%;
    border-radius: 3px;
  }
}
/* ====================== Custom Image Upload with Preview End ==================== */


/* ====================== Apexcharts Style Start ==================== */
.apexcharts-tooltip-z-group {
  display: flex;
}
.apexcharts-title-text {
  font-family: var(--body-font) !important;
  font-weight: 600;
  @include fs-16;
  fill: hsl(var(--black) / .6);
}
/* ====================== Apexcharts Style End ==================== */


/* ====================== CK Editor Start ==================== */
.ck-content {
  height: 250px;
  @include xsm-screen {
    height: 200px;
  }
}
/* ====================== CK Editor End ==================== */


/* ====================== Styled Title Start ====================== */
.styled-title {
  display: inline;
  background-image: linear-gradient(to left, hsl(var(--base)), hsl(var(--base-two)));
  background-size: 25% 3px;
  background-repeat: no-repeat;
  background-position: left 95%;
  @include md-screen {
    background-size: 20% 2px;
    background-position: left 90%;
  }
}
/* ====================== Styled Title End ====================== */


/* ====================== No Data Found In Table Start ====================== */
.no-data-found {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 0;
  @include md-screen {
    padding: 10px 0;
  }
  img {
    max-width: 150px;
    margin: 0 auto 30px;
    @include md-screen {
      max-width: 80px;
      margin-bottom: 20px;
    }
  }
  span {
    display: block;
    @include fs-18;
    line-height: 1;
    font-weight: 500;
    color: hsl(var(--black) / .7);
    @include md-screen {
      @include fs-16;
    }
  }
}
/* ====================== No Data Found In Table End ====================== */