
/* ================================= Social Icon Css Start =========================== */
.social-list {
    display: flex;
    flex-wrap: wrap; 
    align-items: center;
    &-2 {
        .social-list__link {
            border: 1px solid hsl(var(--base) / .5);
        }
    }
    &__item {
        margin-right: 10px;
        &:last-child {
            margin-right: 0;
        }
    }
    &__link {
        width: 40px;
        height: 40px;
        border-radius: 5px;
        position: relative;
        overflow: hidden;
        transition: .3s;
        cursor: pointer;
        color: hsl(var(--white));
        background-color: hsl(var(--base-two));
        &:hover, &:focus  {
            background-color: hsl(var(--base));
            color: hsl(var(--white)) !important;
        }
        @include sm-screen {
            width: 35px;
            height: 35px;
            @include fs-14;
        }
    }
}
/* ================================= Social Icon Css End ===========================  */