/* ============= Footer Start Here ======================= */
.footer-area {
    background: linear-gradient(45deg, hsl(var(--base) / .05), hsl(var(--base-two) / .05), hsl(var(--base) / .05));
    background: hsl(var(--base-d-800));
    background-position: right bottom !important;
    background-size: contain !important;
    margin-top: auto;
    position: relative;
    z-index: 2;
    @include xl-screen {
        padding-top: 80px;
    }
    @include sm-screen {
        padding-top: 60px;
    }
}

.footer-item {
    color: hsl(var(--white) / .75);
    &__logo {
        margin-bottom: 20px;
        a {
            img {
                width: 100%;
                height: 100%;
                max-width: 190px;
                max-height: 64px;
            }
        }
    }
    &__title {
        color: hsl(var(--white));
        padding-bottom: 10px;
        margin-bottom: 25px;
        position: relative;
        &::after {
            position: absolute;
            content: "";
            width: 30px;
            height: 3px;
            left: 0;
            bottom: -2px;
            background-color: hsl(var(--base-two));
        }
    }
    .social-list {
        margin-top: 30px;
        @include md-screen {
            margin-top: 20px;
        }
        @include msm-screen {
            margin-top: 15px;
        }
    }
}
/* Footer List Item */
.footer-menu {
    display: flex;
    flex-direction: column;
    &__item {
        display: block;
        padding-bottom: 16px;
        &:last-child {
            padding-bottom: 0;
        }
    }
    &__link {
        color: hsl(var(--white) / .75);
        &:hover {
            color: hsl(var(--base-two-l-100));
            text-decoration: underline;
        }
    }
}

/* Footer Contact */
.footer-contact-menu {
    &__item {
        display: flex;   
        padding-bottom: 12px;
        color: hsl(var(--white) / .75);
        &:last-child {
            padding-bottom: 0;
        }
        &-icon {
            width: 15px;
            color: hsl(var(--base-two));
            font-size: 1.25rem; // 20px
        }
        &-content {
            width: calc(100% - 15px);
            padding-left: 15px;
        }
    }
}
/* ============= Footer End Here ======================= */

/* ============= Bottom Footer End Here ======================= */
.bottom-footer {
    border-top: 1px solid hsl(var(--white) / .1);
    color: hsl(var(--white) / .65);
}
/* =============Bottom Footer End Here ======================= */