.faq-card {
     background: hsl(var(--white));
     padding: 10px 0 10px 20px;
     border-left: 4px solid hsl(var(--base) / .2);
     height: 100%;
     &__header {
          @include fs-24;
          line-height: 1.1;
          font-weight: 600;
          color: hsl(var(--black) / .6);
          border-bottom: 1px solid hsl(var(--black) / .05);
          padding-left: 1.5rem;
          padding-bottom: 10px;
          margin-bottom: 10px;
          position: relative;
          &::after {
               content: "";
               position: absolute;
               top: 0;
               left: -6px;
               transform: rotate(-45deg) translateY(12px);
               width: 10px;
               height: 10px;
               border-style: double;
               border-color: hsl(var(--base));
               border-width: 6px;
               border-right-width: 1px;
               border-bottom-width: 1px;
          }
     }
     &__desc {
          margin-bottom: 0;
     }
}