/* =========================================== Account Css Start =========================*/
.account {
    display: flex;
    align-items: center;
    min-height: 100vh;
    background: linear-gradient(-45deg, hsl(var(--base-two) / .05), hsl(var(--base) / .05), hsl(var(--base-two) / .05));
    position: relative;
    overflow: hidden;
    z-index: 2;
    &__bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
    }
    &__thumb {
        @include sm-screen {
            width: max-content;
            height: 150px;
            margin: 0 auto 30px;
            img {
                max-height: 100%;
            }
        }
    }
    &__top {
        margin-bottom: 30px;
    }
}
.back-to-home {
    width: 50px;
    height: 50px;
    border: 1px solid hsl(var(--base) / .3);
    background: hsl(var(--white));
    border-radius: 50%;
    color: hsl(var(--base));
    @include fs-30;
    display: flex;
    align-items: center;
    justify-content: center;
    @include md-screen {
        width: 45px;
        height: 45px;
        @include fs-30;
    }
    @include sm-screen {
        width: 45px;
        height: 45px;
        @include fs-30;
    }
    &:hover {
        background: hsl(var(--base));
        color: hsl(var(--white));
    }
}
.account-form  {
    padding: 10px;
    &__title {
        @include fs-30;
        line-height: 1;
        font-weight: 700;
        color: hsl(var(--black) / .6);
        @include md-screen {
            @include fs-25;
        }
    }
}
/* ============================================ Account Css End ====================================*/