/* ================================= Tab Css Start =========================== */
.custom--tab {
    border: 0;
    border-bottom: 1px solid hsl(var(--black) / .05);
    padding-bottom: 10px;
    margin-bottom: 30px;
    gap: 10px;
    @include md-screen {
        margin-bottom: 15px;
    }
    .nav-item {
        border-bottom: 0;
        padding: 5px;
    }
    .nav-link {
        color: hsl(var(--black)); 
        padding: 8px 25px !important;
        background-color: transparent !important;
        border-radius: 5px;
        transition: .4s;
        border: 1px solid hsl(var(--black) / .08) !important;
        margin-bottom: 0;
        @include md-screen {
            padding: 5px 15px !important;
            @include fs-14;
        }
        &.active {
            color: hsl(var(--white)); 
            background-color: hsl(var(--base-d-200)) !important;
            border: 1px solid transparent !important;
            &:hover {
                color: hsl(var(--white)); 
            }
        }
        &:hover {
            color: hsl(var(--base));
        }
    }
}
/* ================================= Tab Css End =========================== */