/* ================================= Table Css Start =========================== */
/* Table Content Css start */
.action-buttons {
	@extend .flex-align; 
	gap: 10px;
	justify-content: flex-end;
}
.action-btn {
	width: 35px;
	height: 35px;
	border-radius: 50%;
}
.edit-btn {
	color: hsl(var(--info));
	background-color: hsl(var(--info) / .08)
}
.delete-btn {
	color: hsl(var(--danger));
	background-color: hsl(var(--danger) / .08); 
}
/* Table Css Start */
.table {
	margin: 0;
	border-collapse: collapse;
	border-collapse: separate;
	border-spacing: 0px 0px;
	border-radius: 10px;
	background-color: hsl(var(--white));
	&.table-borderless {
		th, td {
			&:not(:first-child) {
				border-left: 0;
			}
			&:not(:last-child) {
				border-right: 0;
			}
		}
	}
	&.table--striped {
		>tbody>tr:nth-of-type(odd)>* {
			background: hsl(var(--black) / .025);
		}
	}
	&.table-flush {
		box-shadow: 0 0;
		>* {
			th, td {
				border-radius: 0 !important;
				padding-left: 0;
				padding-right: 0;
				border-left-width: 0 !important;
				border-right-width: 0 !important;
			}
			tr {
				&:first-child {
					th, td {
						padding-top: 0;
						border-top-width: 0 !important;
					}
				}
				&:last-child {
					th, td {
						padding-bottom: 0;
						border-bottom-width: 0 !important;
					}
				}
			}
		}
	}
	thead { 
		tr {
			th {
				text-align: center;
				padding: 7px 15px;
				color: hsl(var(--black) / .75);
				font-family: var(--heading-font);
				font-weight: 500;
                	border-bottom: 0;
				max-width: 170px;
				@extend .fs-18;
				border: 1px solid hsl(var(--black) / .03);
				background-color: hsl(var(--base-l-900));
				backdrop-filter: blur(10px);
				&:not(:first-child) {
					border-left: 0;
				}
				&:first-child {
					text-align: left;
					border-radius: 10px 0 0 0;
				}
				&:last-child {
					border-radius: 0 10px 0 0; 
					text-align: right;
				}
			}
		}
	}
	>*:first-child {
		tr:first-child {
			th, td {
				border-top-width: 1px;
				border-top-style: solid;
				&:first-child {
					border-radius: 10px 0 0 0;
				}
				&:last-child {
					border-radius: 0 10px 0 0;
				}
			}
		}
	}
	tbody {
		border: 0 !important;
		tr {
			border-bottom: 1px solid hsl(var(--black) / .05);
			&:last-child {
				border-bottom: 0;
				td {
					border-bottom: 1px solid hsl(var(--black) / .05);
					&:first-child {
						 border-radius: 0px 0 0 10px;
					}
					&:last-child {
						border-radius: 0 0px 10px 0;
					}
				}
			}
			td {
				text-align: center;
				vertical-align: middle;
				padding: 10px 15px;
				border-width: 1px;
                	border: 0;
				color: hsl(var(--black)/0.7); 
				font-weight: 500;
				max-width: 200px;
				@extend .fs-15;
				border-bottom: 1px solid;
				border-right: 1px solid;
				border-color: hsl(var(--black) / .05);
				&::before {
					content: attr(data-label);
					font-family: var(--heading-font);
					@include fs-16;
					color: hsl(var(--black)); 
					font-weight: 500;
					display: none;
					width: 45% !important;
					text-align: left;
				}
				&:first-child {
					text-align: left;
					border-left: 1px solid hsl(var(--black) / .05);
				}
				&:last-child {
					text-align: right;
					border-right: 1px solid hsl(var(--black) / .05);
				}
				&.no-data-table {
					border-radius: 0 0 10px 10px !important;
					justify-content: center;
					&::before {
						display: none;
					}
				}
			}
		}
	}
}

.table--responsive--sm {
	@include xsm-screen {
		thead {
			display: none;
		}
		tbody {
			tr {
				display: block;
				&:last-child td {
					border-bottom: 0;
				}
				td {
					display: flex;
					flex-wrap: wrap;
					align-items: center;
					justify-content: space-between;
					gap: 10px;
					text-align: right;
					padding: 10px 15px;
					border: none;
					border-bottom: 1px solid hsl(var(--black) / .04) !important;
					max-width: unset;
					&:last-child {
						border-bottom: 0 !important;
						&.no-data-table {
							border-radius: 10px !important;
						}
					}
					&:first-child {
						@include fs-12;
						text-align: right;
						border-left: 0;
						&.no-data-table {
							border-radius: 10px !important;
						}
					}
					&::before {
					    display: block;
						@include fs-16;
					}
					&.no-data-table {
						border-radius: 10px !important;
					}
				}
			}
		}
		tr {
			&:first-child {
				border-radius: 10px 10px 0 0;
				overflow: hidden;
				td {
					&:first-child {
						border-top: 1px solid hsl(var(--black) / .07);
						border-radius: 10px 10px 0 0 !important;
					}
				}
			}
			&:last-child {
				border-radius: 0 0 10px 10px;
				overflow: hidden;
				td {
					&:last-child {
						border-bottom: 1px solid hsl(var(--black) / .07) !important;
						border-radius: 0 0 10px 10px !important;
					}
				}
			}
		}
		td {
			border-radius: 0 !important;
			border-left: 1px solid hsl(var(--black) / .07) !important;
			border-right: 1px solid hsl(var(--black) / .07) !important;
		}
    	}
	@include sm-screen {
		table
        	tbody {
			border-top: 1px solid hsl(var(--black) / .07) !important;
			tr {
				td {
					border: 0;
				}
			}
		}
		td {
			border-bottom: 1px solid hsl(var(--black) / .04) !important;
		}
    	}
}
