.custom--dropdown {
     position: relative;
     &.dropdown-sm {
          .dropdown-menu {
               min-width: 170px;
          }
     }
     .dropdown-menu {
          padding: 5px 10px;
          transform: scaleY(0);
          transform-origin: top center;
          top: 0;
          transition: .3s;
          overflow: hidden;
          box-shadow: 0px 0px 15px 0px hsl(var(--black) / .05);
          border-color: hsl(var(--black) / .05);
          border-radius: 10px;
          min-width: 200px;
          margin-top: 0 !important;
          &-end {
               left: auto;
               right: 0;
          }
          li {
               border-bottom: 1px solid hsl(var(--black) / .03);
               &:last-child {
               border-bottom: 0;
               }
          }
          .dropdown-item{
               display: flex;
               align-items: center;
               gap: 10px;
               padding: 7px 10px;
               font-weight: 500;
               @include fs-16;
               color: hsl(var(--black) / .7);
               border-radius: 5px;
               transition: .3s;
               @include md-screen {
                    padding: 7px 5px;
               }
               &.active {
                    font-weight: 600;
                    color: hsl(var(--base-d-100));
                    background-color: transparent;
               }
               &:focus, &:hover {
                    color: hsl(var(--base-d-100));
                    background-color: transparent;
               }
          }
     }
}