.faq {
     position: relative;
     z-index: 2;
     &__vector-1 {
          position: absolute;
          top: 100px;
          right: 50px;
          width: 120px;
          filter: drop-shadow(0px 40px 30px hsl(var(--black) / .2));
          animation: pulse 3s linear infinite;
          opacity: 0.7;
          z-index: -1;
          @include lg-screen {
               top: 80px;
               right: 30px;
               width: 80px;
               filter: drop-shadow(0px 15px 15px hsl(var(--black) / .2));
          }
          @include md-screen {
               top: 25px;
               right: 20px;
               width: 40px;
               filter: drop-shadow(0px 10px 10px hsl(var(--black) / .2));
          }
     }
}