.dashboard {
    &__user-info {
        background: linear-gradient(45deg, hsl(var(--base) / .03), hsl(var(--base-two) / .03), hsl(var(--base) / .03));
        border: 1px solid hsl(var(--dark) / .05);
        border-radius: 10px;
        padding: 30px;
        @include sm-screen {
            padding: 20px;
        }
        &__name {
            @include fs-25;
            line-height: 1;
            color: hsl(var(--base-d-300));
            margin-bottom: 10px;
            @include sm-screen {
                @include fs-22;
                margin-bottom: 5px;
            }
        }
        &__email {
            @include fs-22;
            line-height: 1;
            font-weight: 600;
            border-bottom: 1px solid hsl(var(--black) / .05);
            padding-bottom: 15px;
            margin-bottom: 25px;
            @include sm-screen {
                @include fs-18;
            }
        }
        &__list {
            @include fs-15;
            line-height: 1;
            display: flex;
            flex-wrap: wrap;
            margin: -15px;
            li {
                width: calc(100% / 3);
                display: flex;
                flex-direction: column;
                gap: 10px;
                padding: 15px;
                @include sm-screen {
                    width: calc(100% / 2);
                }
                @include xsm-screen {
                    width: 100%;
                }
            }
        }
    }
}

.dashboard-card {
    background: hsl(var(--base) / .05);
    border: 1px solid hsl(var(--black) / .04);
    border-radius: 10px;
    height: 100%;
    padding: 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    overflow: hidden;
    position: relative;
    z-index: 2;
    &::after {
        content: "";
        position: absolute;
        top: -30px;
        right: -30px;
        height: 100%;
        aspect-ratio: 1 / 1;
        background: hsl(var(--base-l-500) / .1);
        border-radius: 50%;
        transition: .3s;
        z-index: -1;
    }
    @include md-screen {
        padding: 10px;
    }
    @include xsm-screen {
        max-width: 320px;
        margin: auto;
    }
    &__row {
        >*:nth-child(6n + 2) {
            .dashboard-card {
                background: hsl(var(--danger) / .05);
                &::after {
                    background: hsl(var(--danger-d-500) / .05);
                }
                &__icon {
                    background: linear-gradient(hsl(var(--danger)), hsl(var(--danger-d-500)));
                }
                &__title {
                    color: hsl(var(--danger));
                }
                &__subtitle {
                    color: hsl(var(--danger-d-400));
                }
            }
        }
        >*:nth-child(6n + 3) {
            .dashboard-card {
                background: hsl(var(--success) / .05);
                &::after {
                    background: hsl(var(--success-d-500) / .05);
                }
                &__icon {
                    background: linear-gradient(hsl(var(--success)), hsl(var(--success-d-500)));
                }
                &__title {
                    color: hsl(var(--success));
                }
                &__subtitle {
                    color: hsl(var(--success-d-400));
                }
            }
        }
        >*:nth-child(6n + 4) {
            .dashboard-card {
                background: hsl(var(--warning) / .05);
                &::after {
                    background: hsl(var(--warning-d-500) / .05);
                }
                &__icon {
                    background: linear-gradient(hsl(var(--warning)), hsl(var(--warning-d-500)));
                }
                &__title {
                    color: hsl(var(--warning));
                }
                &__subtitle {
                    color: hsl(var(--warning-d-400));
                }
            }
        }
        >*:nth-child(6n + 5) {
            .dashboard-card {
                background: hsl(var(--info) / .05);
                &::after {
                    background: hsl(var(--info-d-500) / .05);
                }
                &__icon {
                    background: linear-gradient(hsl(var(--info)), hsl(var(--info-d-500)));
                }
                &__title {
                    color: hsl(var(--info));
                }
                &__subtitle {
                    color: hsl(var(--info-d-400));
                }
            }
        }
        >*:nth-child(6n + 6) {
            .dashboard-card {
                background: hsl(var(--base-two) / .05);
                &::after {
                    background: hsl(var(--base-two-d-500) / .1);
                }
                &__icon {
                    background: linear-gradient(hsl(var(--base-two)), hsl(var(--base-two-d-500)));
                }
                &__title {
                    color: hsl(var(--base-two));
                }
                &__subtitle {
                    color: hsl(var(--base-two-d-400));
                }
            }
        }
    }
    &__icon {
        width: 70px;
        height: 70px;
        border-radius: 50%;
        overflow: hidden;
        background: linear-gradient(hsl(var(--base)), hsl(var(--base-d-500)));
        display: flex;
        justify-content: center;
        align-items: center;
        @include md-screen {
            width: 50px;
            height: 50px;
        }
        img {
            min-height: 100%;
        }
    }
    &__txt {
        width: calc(100% - 90px);
        text-align: right;
        @include md-screen {
            width: calc(100% - 60px);
        }
    }
    &__title {
        display: block;
        @include fs-18;
        font-weight: 500;
        line-height: 1;
        color: hsl(var(--base));
        margin-bottom: 10px;
        @include md-screen {
            @include fs-14
        }
    }
    &__number {
        display: block;
        font-family: var(--heading-font);
        @include fs-25;
        line-height: 1;
        font-weight: 600;
        color: hsl(var(--black) / .5);
        margin-bottom: 5px;
        @include md-screen {
            @include fs-20;
        }
    }
    &__subtitle {
        display: block;
        font-family: var(--heading-font);
        @include fs-18;
        font-weight: 600;
        line-height: 1;
        color: hsl(var(--base-d-300));
        @include md-screen {
            @include fs-14
        }
    }
    &:hover {
        border-color: hsl(var(--black) / .15);
        &::after {
            transform: scale(1.2);
        }
    }
}


.account-setup-key,
.refer-link {
    &__badge {
        position: absolute;
        top: 50%;
        right: 60px;
        transform: translateY(-50%);
        transition: .3s;
        opacity: 0;
        &.show {
            opacity: 1;
        }
    }
    &__copy {
        padding: 0;
        width: 50px;
        @include fs-20;
    }
}

.profile-settings {
    &__sidebar {
        border-right: 1px solid hsl(var(--black) / .05);
        padding-right: 25px;
        @include md-screen {
            border-right: 0;
            padding-right: 0;
        }
        .upload__img {
            margin-bottom: 25px;
        }
    }
}
.user-profile-list {
    @include fs-16;
    font-weight: 600;
    height: 100%;
    li {
        padding: 15px 0;
        border-bottom: 1px dashed hsl(var(--black) / .08);
        justify-content: space-between;
        &:first-child {
            padding-top: 0;
            @include md-screen {
                padding-top: 15px;
            }
        }
        &:last-child {
            border-bottom: 0;
            padding-bottom: 0;
            @include md-screen {
                padding-bottom: 15px;
                border-bottom: 1px dashed hsl(var(--black) / .08);
            }
        }
        span {
            display: block;
            font-family: var(--heading-font);
            font-weight: 500;
            i {
                @include fs-14;
                margin-right: 5px;
            }
        }
    }
}

.qr-code-img {
    max-width: 150px;
    margin: 15px auto;
}

.exchange-details-list {
    @include fs-16;
    font-weight: 700;
    color: hsl(var(--black) / .6);
    li {
        border-bottom: 1px dashed hsl(var(--black) / .08);
        padding: 15px 0;
        &:first-child {
            padding-top: 0;
        }
        &:last-child {
            border-bottom: 0;
            padding-bottom: 0;
        }
    }
    span {
        display: block;
        @include fs-14;
        font-family: var(--heading-font);
        font-weight: 500;
    }
}

.exchange-information-list {
    @include fs-16;
    font-weight: 700;
    color: hsl(var(--black) / .6);
    li {
        border-bottom: 1px dashed hsl(var(--black) / .08);
        padding: 15px 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        &:first-child {
            padding-top: 0;
        }
        &:last-child {
            border-bottom: 0;
            padding-bottom: 0;
        }
    }
    span {
        @include fs-15;
        font-weight: 500;
    }
    .badge {
        @include fs-12;
    }
}

.step {
    &__list {
        display: flex;
        gap: 10px;
        justify-content: space-between;
        @include fs-18;
        line-height: 1;
        font-weight: 600;
        position: relative;
        margin-bottom: 40px;
        @include md-screen {
            @include fs-16;
            margin-bottom: 25px;
        }
        @include sm-screen {
            @include fs-14;
        }
        @include xsm-screen {
            @include fs-12;
        }
        &::after {
            content: "";
            position: absolute;
            top: 50%;
            left: 0;
            right: 0;
            height: 2px;
            background: hsl(var(--black) / .1);
            z-index: -1;
        }
        li {
            background: hsl(var(--white));
            box-shadow: 0px 0px 30px 0px hsl(var(--black) / .05);
            border-radius: 5px;
            padding: 55px 25px 15px;
            text-align: center;
            position: relative;
            overflow: hidden;
            transition: .3s;
            @include md-screen {
                padding: 48px 15px 15px;
            }
            @include sm-screen {
                padding: 43px 10px 10px;
            }
            &::before {
                content: "";
                position: absolute;
                bottom: 0;
                left: 0;
                right: 0;
                height: 3px;
                background: hsl(var(--base));
                opacity: 0.2;
                transition: .3s;
                @include sm-screen {
                    height: 2px;
                }
            }
            &::after {
                content: "\f00c";
                position: absolute;
                top: 15px;
                left: 50%;
                width: 25px;
                height: 25px;
                transform: translateX(-50%);
                font-family: "Font Awesome 5 Free";
                font-weight: 900;
                @include fs-14;
                color: hsl(var(--base) / .2);
                background: hsl(var(--white));
                border: 2px solid hsl(var(--base) / .1);
                border-radius: 50%;
                display: flex;
                justify-content: center;
                align-items: center;
                transition: .3s;
                @include md-screen {
                    width: 20px;
                    height: 20px;
                    @include fs-10;
                }
            }
            &.active {
                border-color: hsl(var(--base));
                &::before {
                    opacity: 1;
                }
                &::after {
                    background: hsl(var(--base));
                    border-color: hsl(var(--base));
                    color: hsl(var(--white));
                }
            }
        }
    }
    &__card {
        &.hide {
            animation: slideOut .3s linear;
            opacity: 0;
        }
        &.show {
            animation: slideIn .3s linear;
            opacity: 1;
        }
    }
    &__title {
        @include fs-25;
        line-height: 1;
        font-weight: 500;
        color: hsl(var(--black) / .6);
        border-bottom: 1px solid hsl(var(--black) / .1);
        padding-bottom: 10px;
        margin-bottom: 25px;
        display: flex;
        align-items: center;
        gap: 5px;
        @include md-screen {
            @include fs-18;
        }
        i {
            @include fs-30;
            @include md-screen {
                @include fs-23;
            }
        }
    }
    &__success-msg {
        text-align: center;
        padding: 50px 0;
        &__thumb {
            max-width: 350px;
            margin: 0 auto 30px;
            @include sm-screen {
                max-width: 250px;
            }
        }
        &__title {
            @include fs-35;
            line-height: 1;
            font-weight: 700;
            margin-bottom: 30px;
            @include sm-screen {
                @include fs-25;
            }
        }
    }
}
.buy-sell-select {
    display: flex;
    justify-content: center;
    gap: 20px;
    @include sm-screen {
        gap: 10px;
    }
    &__checkbox {
        text-align: center;
        label {
            display: block;
            color: hsl(var(--black) / .6);
            background: hsl(var(--base) / .03);
            border: 1px solid hsl(var(--base) / .05);
            border-radius: 10px;
            padding: 25px 50px;
            @include fs-20;
            line-height: 1.5;
            font-weight: 500;
            transition: .3s;
            cursor: pointer;
            @include sm-screen {
                border-radius: 7px;
                padding: 15px;
                @include fs-14;
            }
            i {
                display: block;
                @include fs-45;
                color: hsl(var(--base-l-600));
                margin-bottom: 10px;
                @include sm-screen {
                    @include fs-30;
                }
            }
        }
        input:checked + label {
            border-color: hsl(var(--base-l-500));
        }
    }
}
.price-result {
    &__inner {
        display: flex;
        justify-content: center;
        position: relative;
        z-index: 2;
        &::after {
            content: "";
            position: absolute;
            top: 50%;
            left: 0;
            width: 100%;
            height: 1px;
            background: hsl(var(--black) / .1);
            z-index: -1;
        }
        span {
            border: 1px solid hsl(var(--black) / .05);
            border-radius: 5px;
            background: hsl(var(--base-l-900));
            padding: 10px 15px;
            @include fs-20;
            line-height: 1;
            font-weight: 600;
            color: hsl(var(--base) / .7);
            @include md-screen {
                padding: 5px 10px;
                @include fs-14;
            }
        }
    }
}

.trade-chat {
    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-bottom: 1px solid hsl(var(--black) / .05);
        padding-bottom: 15px;
    }
    &__username {
        @include fs-20;
        line-height: 1;
        font-weight: 600;
        color: hsl(var(--black) / .6);
        i {
            @include fs-22;
            line-height: 1;
            color: hsl(var(--base-two));
        }
    }
    &__chatbox {
        margin: 20px 0;
        max-height: 300px;
        overflow: auto;
        &::-webkit-scrollbar {
            width: 4px;
            height: 10px;
        }
        &::-webkit-scrollbar-thumb {
            background-color: hsl(var(--black) / .15);
        }
        &::-webkit-scrollbar-track {
            background-color: hsl(var(--black) / .1);
        }
    }
    &__msg {
        display: flex;
        gap: 10px;
        width: max-content;
        max-width: 70%;
        margin-bottom: 20px;
        @include xsm-screen {
            max-width: 80%;
        }
        &:last-child {
            margin-bottom: 0;
        }
        &.outgoing {
            margin-left: auto;
            .trade-chat__msg__icon {
                color: hsl(var(--base));
            }
            .trade-chat__msg__txt {
                background: hsl(var(--base) / .06);
                color: hsl(var(--black) / .8);
                border-radius: 5px 0 5px 5px;
            }
        }
        &__icon {
            width: 30px;
            @include fs-30;
            line-height: 1;
            color: hsl(var(--black) / .4);
        }
        &__content {
            max-width: calc(100% - 40px);
        }
        &__txt {
            width: max-content;
            max-width: 100%;
            background: hsl(var(--black) / .02);
            padding: 5px 10px;
            border: 1px solid hsl(var(--black) / .04);
            border-radius: 0 5px 5px 5px;
            margin-bottom: 5px;
            p {
                @include fs-14;
            }
        }
        &__date {
            display: block;
            @include fs-13;
            line-height: 1;
        }
    }
    &__type-msg {
        border: 1px solid hsl(var(--black) / .1);
        border-radius: 10px;
        padding: 10px;
        transition: .3s;
        &:focus-within {
            border-color: hsl(var(--base));
        }
        textarea {
            width: 100%;
            resize: none;
            border: 0;
            outline: none;
            &::-webkit-scrollbar {
                width: 4px;
                height: 10px;
            }
            &::-webkit-scrollbar-thumb {
                background-color: hsl(var(--black) / .15);
            }
            &::-webkit-scrollbar-track {
                background-color: hsl(var(--black) / .1);
            }
        }
    }
    &__show-attach {
        width: 70px;
        height: 70px;
        border-radius: 5px;
        background: hsl(var(--black) / .05);
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 10px;
        display: none;
        position: relative;
        &.active {
            display: block;
        }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 5px;
        }
    }
    &__attachment {
        width: 100%;
        height: 100%;
        .file-container {
            width: 100%;
            height: 100%;
            display: flex;
            gap: 7px;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            color: hsl(var(--black) / .5);
            i {
                @include fs-30;
            }
        }
    }
    &__clear-attach {
        position: absolute;
        top: 0;
        right: 0;
        text-align: center;
        width: 18px;
        height: 18px;
        line-height: 18px;
        background: red;
        color: hsl(var(--white));
        border-radius: 50%;
        @include fs-14;
        transform: translate(25%, -25%);
    }
}

.wallet {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 10px;
    margin-bottom: 25px;
    &__card {
        @include fs-14;
        font-weight: 500;
        height: 40px;
        border: 1px solid hsl(var(--base) / .1);
        border-radius: 20px;
        padding: 0 5px;
        display: flex;
        align-items: center;
        @include md-screen {
            @include fs-13;
            height: 35px;
        }
        &:hover, &.active {
            background: hsl(var(--base));
            border-color: hsl(var(--base));
            color: hsl(var(--white));
            .wallet__card__badge {
                background: hsl(var(--white));
                color: hsl(var(--base));
            }
        }
        &__badge {
            background: hsl(var(--base));
            color: hsl(var(--white));
            @include fs-12;
            font-weight: 600;
            height: 30px;
            line-height: 30px;
            padding: 0 10px;
            border-radius: 15px;
            transition: .3s;
            @include md-screen {
                height: 25px;
                line-height: 25px;
                padding: 0 8px;
            }
        }
        &__number {
            padding: 0 15px;
            @include md-screen {
                padding: 0 10px;
            }
        }
    }
}

.withdraw {
    &__card {
        background: hsl(var(--base-d-600));
        border-radius: 10px;
        height: 100%;
        display: flex;
        align-items: center;
        gap: 30px;
        padding: 25px;
        position: relative;
        z-index: 2;
        @include md-screen {
            padding: 20px;
            gap: 20px;
        }
        &::after {
            content: "";
            position: absolute;
            top: 50%;
            left: 0;
            height: 70px;
            width: 80px;
            transform: translateY(-50%);
            background: hsl(var(--white) / .15);
            border-radius: 0 35px 35px 0;
            z-index: -1;
            @include md-screen {
                height: 60px;
                width: 62px;
            }
        }
        &__icon {
            text-align: center;
            width: 40px;
            color: hsl(var(--base-l-900));
            @include fs-40;
            @include md-screen {
                width: 30px;
                @include fs-30;
            }
        }
        &__content {
            width: calc(100% - 70px);
            @include md-screen {
                width: calc(100% - 50px);
            }
        }
        &__title {
            display: block;
            @include fs-16;
            line-height: 1;
            font-weight: 500;
            color: hsl(var(--white) / .9);
            margin-bottom: 13px;
            @include md-screen {
                @include fs-14
            }
        }
        &__number {
            display: block;
            font-family: var(--heading-font);
            @include fs-25;
            line-height: 1;
            font-weight: 600;
            color: hsl(var(--base-two-d-200));
            @include md-screen {
                @include fs-20;
            }
        }
    }
}

@keyframes slideIn {
    0% {
        transform: translateX(50%);
        opacity: 0;
    }
    100% {
        transform: translate(0);
        opacity: 1;
    }
}
@keyframes slideOut {
    0% {
        transform: translateX(0);
        opacity: 1;
    }
    100% {
        transform: translate(-50%);
        opacity: 0;
    }
}