.custom--progress {
     height: 8px;
     overflow: visible;
     .progress-bar {
          background: linear-gradient(to right, hsl(var(--base)), hsl(var(--base-two)));
          border-radius: 4px;
          position: relative;
          overflow: visible;
          .progress-txt {
               position: absolute;
               left: 100%;
               bottom: calc(100% + 2px);
               background: hsl(var(--base));
               padding: 3px 5px;
               line-height: 1;
               border-radius: 2px;
               transform: translateX(-50%);
               z-index: 2;
               &::after {
                    content: "";
                    position: absolute;
                    bottom: -3px;
                    left: 50%;
                    width: 4px;
                    height: 4px;
                    background: hsl(var(--base));
                    transform: rotate(45deg) translateX(-50%);
                    z-index: -1;
               }
          }
     }
}