.main-sidebar {
     position: fixed;
     top: 70px;
     bottom: 0;
     left: 0;
     width: 300px;
     border-right: 1px solid hsl(var(--black) / .05);
     background: hsl(var(--white));
     padding: 20px 11px 20px 15px;
     transition: .3s;
     overflow: scroll;
     z-index: 11;
     &.active {
          transform: translate(0);
     }
     @include md-screen {
          transform: translateX(-100%);
     }
     &::-webkit-scrollbar {
          width: 4px;
          height: 0;
     }
     &::-webkit-scrollbar-track {
          background: transparent;
     }
     &::-webkit-scrollbar-thumb {
          background: hsl(var(--black) / .2);
          border-radius: 2px;
     }
     &::-webkit-scrollbar-thumb:hover {
          background: hsl(var(--black) / .4);
     }
     .form {
          margin-bottom: 20px;
          position: relative;
          transition: .3s;
          input {
               width: 100%;
               height: 40px;
               border: 1px solid hsl(var(--black) / .1);
               border-radius: 20px;
               outline: none;
               padding: 0 20px;
               background: transparent;
               &:focus {
                    border-color: hsl(var(--base));
               }
          }
          ul {
               position: absolute;
               top: 100%;
               left: 0;
               width: 100%;
               background: hsl(var(--white));
               border: 1px solid hsl(var(--black) / .1);
               border-radius: 10px;
               padding: 10px;
               max-height: 400px;
               overflow: auto;
               z-index: 2;
               li {
                    padding: 10px;
                    border-bottom: 1px solid hsl(var(--black) / .05);
                    border-radius: 5px;
                    transition: .3s;
                    a {
                         display: -webkit-box;
                         text-overflow: ellipsis;
                         -webkit-line-clamp: 2;
                         -webkit-box-orient: vertical;
                         overflow: hidden;
                    }
                    &:hover {
                         background: hsl(var(--base-l-900));
                         border-color: transparent;
                    }
               }
          }
     }
     .sidebar-item {
          font-size: 14px;
          font-weight: 500;
          margin-bottom: 5px;
          &:last-child {
               margin-bottom: 0;
          }
          .sidebar-link {
               display: block;
               padding: 10px 15px;
               border-radius: 20px;
               position: relative;
               color: hsl(var(--black) / .6);
               font-weight: 600;
               transition: .3s;
               &.has-sub {
                    position: relative;
                    &::after {
                         content: "\f105";
                         font-family: "Font Awesome 5 Free";
                         font-weight: 900;
                         position: absolute;
                         top: 50%;
                         transform: translateY(-50%);
                         right: 15px;
                         font-size: 10px;
                         transition: .3s;
                         opacity: 0.8;
                    }
               }
               &.show {
                    background: hsl(var(--base-l-900));
                    color: hsl(var(--base));
                    &:hover {
                         color: hsl(var(--base));
                    }
                    &::after {
                         transform: translateY(-50%) rotate(90deg);
                    }
               }
               .nav-icon {
                    width: 20px;
                    margin-right: 5px;
               }
               &:hover, &:focus {
                    background: hsl(var(--base-l-900));
                    color: hsl(var(--black) / .7);
               }
               &.highlight {
                    color: hsl(var(--warning)) !important;
                    &::before {
                         content: "";
                         position: absolute;
                         top: 50%;
                         left: 0;
                         transform: translateY(-50%);
                         width: 100%;
                         height: 20px;
                         background: hsl(var(--warning) / .1);
                    }
               }
          }
          &.open {
               .sidebar-link {
                    background: hsl(var(--base));
                    color: hsl(var(--white));
               }
               .sidebar-dropdown-item {
                    .sidebar-link {
                         background: transparent;
                         padding: 10px 0;
                         color: hsl(var(--black) / .6);
                         &.active {
                              color: hsl(var(--base));
                              font-weight: 700;
                              &::before {
                                  content: "";
                                  position: absolute;
                                  top: 50%;
                                  right: 100%;
                                  width: 10px;
                                  height: 2px;
                                  background: hsl(var(--base-l-500));
                                  transform: translateY(-50%);
                              }
                         }
                    }
               }
          }
          > .sidebar-dropdown-menu {
               margin-left: 15px;
          }
          .sidebar-dropdown-menu {
               display: none;
               border-left: 1px solid hsl(var(--base-l-800));
               padding-left: 10px;
          }
          .sidebar-dropdown-item {
               .sidebar-link {
                    background: transparent;
                    color: hsl(var(--black) / .6);
                    padding: 10px 0;
                    &.show, &.active, &:hover, &:focus {
                        color: hsl(var(--base));
                    }
               }
          }
     }
}