/* ================================ Testimonails Section Css Start ============================= */
.testimonial {
    position: relative;
    z-index: 2;
    &::after {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        width: 60%;
        background: linear-gradient(45deg, hsl(var(--base) / .05), hsl(var(--base-two) / .05), hsl(var(--base) / .05));
        border-radius: 70px 0 0 70px;
        z-index: -1;
    }
    &__vector-1 {
        position: absolute;
        top: 0;
        right: 0;
        width: 250px;
        opacity: 0.1;
        z-index: -1;
        @include lg-screen {
            width: 200px;
        }
        @include md-screen {
            width: 150px;
        }
        @include sm-screen {
            width: 100px;
        }
    }
    &__vector-2 {
        position: absolute;
        bottom: -50px;
        left: 100px;
        width: 100px;
        filter: drop-shadow(0px 30px 25px hsl(var(--black) / .4));
        animation: pulse 3s linear infinite;
        opacity: 0.7;
        z-index: -1;
        @include lg-screen {
            bottom: -20px;
            left: 50px;
            width: 70px;
        }
        @include md-screen {
            bottom: -10px;
            left: 10px;
            width: 35px;
            filter: drop-shadow(0px 10px 10px hsl(var(--black) / .4));
        }
    }
}

.testimonial-img-slider {
    &__wrap {
        position: relative;
        z-index: 2;
        &__vector-1 {
            position: absolute;
            bottom: calc(100% - 80px);
            right: calc(100% - 80px);
            width: 150px;
            opacity: 0.2;
            z-index: -1;
            @include lg-screen {
                bottom: calc(100% - 75px);
                right: calc(100% - 75px);
                width: 120px;
            }
            @include md-screen {
                bottom: calc(100% - 75px);
                right: calc(100% - 75px);
                width: 100px;
            }
            @include sm-screen {
                bottom: calc(100% - 55px);
                right: calc(100% - 70px);
                width: 70px;
            }
        }
        &__vector-2 {
            position: absolute;
            top: calc(100% - 80px);
            left: calc(100% - 80px);
            width: 150px;
            opacity: 0.2;
            z-index: -1;
            @include lg-screen {
                top: calc(100% - 75px);
                left: calc(100% - 75px);
                width: 120px;
            }
            @include md-screen {
                top: calc(100% - 75px);
                left: calc(100% - 75px);
                width: 100px;
            }
            @include sm-screen {
                top: calc(100% - 55px);
                left: calc(100% - 70px);
                width: 70px;
            }
        }
    }
    .slick-arrow {
        position: absolute;
        bottom: 20px;
        left: 20px;
        @include sm-screen {
            display: none !important;
        }
        &.slick-next {
            transform: translateX(60px);
        }
    }
}
.testimonial-img-slider__slide {
    border-radius: 10px;
    vertical-align: middle;
    overflow: hidden;
}

.testimonial-card {
    background: hsl(var(--white) / .1);
    height: 100%;
    position: relative;
    overflow: hidden;
    vertical-align: middle;
    z-index: 2;
    @include sm-screen {
        text-align: center;
    }
    &__icon {
        display: block;
        @include fs-70;
        line-height: 1;
        color: hsl(var(--base-two));
        margin-top: -5px;
        margin-bottom: 20px;
        @include lg-screen {
            @include fs-60;
            margin-top: 0;
        }
        @include md-screen {
            @include fs-50;
            margin-top: 0px;
            margin-bottom: 10px;
        }
    }
    &__desc {
        @include fs-20;
        margin-bottom: 20px;
        transform: translateY(100px);
        opacity: 0;
        @include lg-screen {
            @include fs-18;
        }
        @include md-screen {
            @include fs-16;
        }
    }
    &__name {
        @include fs-30;
        line-height: 1;
        font-weight: 600;
        color: hsl(var(--base-l-200));
        margin-bottom: 14px;
        transform: translateY(150px);
        opacity: 0;
        @include md-screen {
            @include fs-25;
            margin-bottom: 10px;
        }
    }
    &__designation {
        display: block;
        @include fs-18;
        line-height: 1;
        transform: translateY(200px);
        opacity: 0;
        @include md-screen {
            @include fs-14;
        }
    }
}

.slick-active {
    .testimonial-card {
        &__name,
        &__designation,
        &__desc {
            transform: translate(0);
            opacity: 1;
            transition: 1s;
        }
    }
}
/* ================================ Testimonails Section Css End ============================= */