/* ================================= Card Css Start =========================== */
.custom--card {
    display: block;
    background-color: hsl(var(--white));
    border: 1px solid hsl(var(--black) / .05);
    border-radius: 10px;
    .card-header {
        padding: 12px 24px;
        background-color: hsl(var(--base-d-600));
        color: hsl(var(--white));
        border-radius: 9px 9px 0 0;
        @include lg-screen {
            padding: 10px 15px;
        }
        @include md-screen {
            padding: 10px 12px;
        }
        .title {
            @include fs-20;
            font-weight: 600;
            letter-spacing: .5px;
            color: hsl(var(--white));
            margin-bottom: 0;
            @include lg-screen {
                @include fs-18
            }
            @include md-screen {
                @include fs-18;
            }
        }
    }
    .card-subtitle {
        font-family: var(--body-font);
        @include fs-20;
        font-weight: 600;
        color: hsl(var(--black) / .6);
    }
    .card-body {
        padding: 24px;
        @include lg-screen {
            padding: 15px;
        }
        @include md-screen {
            padding: 12px;
        }
        .card-title {
            @include fs-18;
            color: hsl(var(--black) / .6);
            margin-bottom: 20px;
        }
    }
    .card-footer {
        padding: 12px 24px;
        background-color: transparent;
        border-top: 1px solid hsl(var(--black) /.1);
    }
}
/* ================================= Card Css End =========================== */