/* =========================== Accordion Css start ============================= */ 
.custom--accordion {
    .accordion-item {
        color: hsl(var(--black) / .7);
        border: 0;
        box-shadow: 0px 0px 25px 0px hsl(var(--base) / .07);
        background-color: hsl(var(--white)) !important;
        border-radius: 10px;
        overflow: hidden;
        &:not(:last-child) {
            margin-bottom:20px;
        }
    }
    .accordion-header {
        line-height: 1;
    }
    .accordion-body {
        padding: 20px;
        line-height: 1.8;
        @include md-screen {
            @include fs-14;
        }
        .text {
            max-width: 60%;
            @include msm-screen {
                max-width: 100%;
            }
        }
    }
    &:first-of-type .accordion-button.collapsed {
        border-radius: 0;
    }
    &:last-of-type .accordion-button.collapsed {
        border-radius: 0;  
    }
    &:first-of-type .accordion-button {
        border-radius: 0;
    }
    .accordion-button{
        @include fs-20;
        line-height: 1.3;
        font-weight: 600;
        color: hsl(var(--black) / .7);
        border-bottom: 1px solid transparent;
        padding: 15px;
        @include lg-screen {
            padding: 10px 15px;
        }
        &::after {
            background-image: none;
            @include fs-25;
            @include md-screen {
                @include fs-18;
            }
        }
        &:focus {
            box-shadow: none;
        }
        &:not(.collapsed) {
            color: hsl(var(--white)); 
            background-color: hsl(var(--base)) !important;
            border-color: hsl(var(--black) / .05);
            box-shadow: none;
            &::after {
                background-image: none;
                color: hsl(var(--white)); ; 
            }
        }
        &[aria-expanded="true"], &[aria-expanded="false"] {
            &::after {
                font-family: "Line Awesome Free";
                font-weight: 900;
                content: "\f068";
                display: inline-block;
                position: absolute;
                @extend .top-center-extend; 
                right: 15px;
                height: unset;
            }
        }
        &[aria-expanded="false"] {
            &::after {
                content: "\f067";
                color: hsl(var(--body-color));
            }
        }
    }
}
/* ================================= Accordion Css End =========================== */