/* ================================= Custom Classes Css Start =========================== */
/* Column Extra Small Screen */
@media (min-width: 480px) and (max-width: 575px) {
    .col-xsm-1 {
        flex: 0 0 auto;
        width: 8.33%;
    }
    .col-xsm-2 {
        flex: 0 0 auto;
        width: 16.67%;
    }
    .col-xsm-3 {
        flex: 0 0 auto;
        width: 25%;
    }
    .col-xsm-4 {
        flex: 0 0 auto;
        width: 33.33%;
    }
    .col-xsm-5 {
        flex: 0 0 auto;
        width: 41.67%;
    }
    .col-xsm-6 {
        flex: 0 0 auto;
        width: 50%;
    }
    .col-xsm-7 {
        flex: 0 0 auto;
        width: 58.33%;
    }
    .col-xsm-8 {
        flex: 0 0 auto;
        width: 66.67%;
    }
    .col-xsm-9 {
        flex: 0 0 auto;
        width: 75%;
    }
    .col-xsm-10 {
        flex: 0 0 auto;
        width: 83.33%;
    }
    .col-xsm-11 {
        flex: 0 0 auto;
        width: 91.67%;
    }
    .col-xsm-12 {
        flex: 0 0 auto;
        width: 100%;
    }
}

/* Section Background */
.section-bg {
    background-color: hsl(var(--section-bg));
}

/* Bg Image Css */
.bg-img {
    background-size: cover !important;
    background-repeat: no-repeat !important;
    background-position: center center !important;
    width: 100%;
    height: 100%;
}

/* Bg Light Gradient Css */
.bg-light-gradient {
    background: linear-gradient(-45deg, hsl(var(--base-two) / .04), hsl(var(--base) / .04), hsl(var(--base-two) / .04));
}

/* Hide Scroll bar Css For Custom Modal */
.scroll-hide {
    position: absolute;
    overflow-y: hidden;
    padding-right: 17px;
    top: 0;
    left: 0;
    width: 100%;
    @include md-screen {
        padding-right: 0;
    }
}
.scroll-hide-sm {
    position: absolute;
    overflow-y: hidden;
    top: 0;
    left: 0;
    width: calc(100% - 0px);
}
/* Overlay Start */
.body-overlay {
    position: fixed;
    width: 100%;
    height: 100%;
    content: "";
    left: 0;
    top: 0;
    background-color: hsl(var(--black) / .6);
    z-index: 99;
    transition: .2s linear;
    visibility: hidden;
    opacity: 0;
    &.show-overlay {
        visibility: visible;
        opacity: 1;
    }
}

.sidebar-overlay {
    position: fixed;
    width: 100%;
    height: 100%;
    content: "";
    left: 0;
    top: 0;
    background-color: hsl(var(--black) / .6);
    z-index: 99;
    transition: .2s linear;
    visibility: hidden;
    opacity: 0;
    &.show {
        visibility: visible;
        opacity: 1;
        z-index: 999;
    }
}
/* Overlay End */


/* Hide text after 1st line */
.text-overflow-1 {
    display: -webkit-box;
    text-overflow: ellipsis;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
}
/* Hide text after 2nd line */
.text-overflow-2 {
    display: -webkit-box;
    text-overflow: ellipsis;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
}
/* Hide text after 3rd line */
.text-overflow-3 {
    display: -webkit-box;
    text-overflow: ellipsis;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
}
/* Hide text after 4th line */
.text-overflow-4 {
    display: -webkit-box;
    text-overflow: ellipsis;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
    overflow: hidden;
}
/* Hide text after 5th line */
.text-overflow-5 {
    display: -webkit-box;
    text-overflow: ellipsis;
    -webkit-line-clamp: 5;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

// Remove Box Shadow
.no-shadow {
    box-shadow: 0 0 !important;
}

// Text Muted
.text--muted {
    color: hsl(var(--black) / .5);
}
/* ================================= Custom Classes Css End =========================== */