/* ================================= Badge Css Start =========================== */
.badge {
    font-family: var(--body-font) !important;
    display: inline-block !important;
    @extend .fs-12; 
    border: 1px solid;
    border-radius: 15px;
    padding: 5px 15px;
    font-weight: 600;
    position: relative;
    text-align: center;
    &--base {
        background-color: hsl(var(--base) / .1) !important; 
        color: hsl(var(--base)) !important;
        border-color: hsl(var(--base) / .2);
    }
    &--base-two {
        background-color: hsl(var(--base-two) / .1) !important; 
        color: hsl(var(--base-two-d-200)) !important;
        border-color: hsl(var(--base-two) / .35);
    }
    &--primary {
        background-color: hsl(var(--primary) / .1) !important; 
        color: hsl(var(--primary)) !important;
        border-color: hsl(var(--primary) / .2);
    }
    &--secondary {
        background-color: hsl(var(--secondary) / .1) !important; 
        color: hsl(var(--secondary)) !important;
        border-color: hsl(var(--secondary) / .2);
    }
    &--success {
        background-color: hsl(var(--success) / .1) !important; 
        color: hsl(var(--success)) !important;
        border-color: hsl(var(--success) / .2);
    }
    &--danger {
        background-color: hsl(var(--danger) / .1) !important; 
        color: hsl(var(--danger)) !important;
        border-color: hsl(var(--danger) / .2);
    }
    &--warning {
        background-color: hsl(var(--warning) / .1) !important; 
        color: hsl(var(--warning)) !important;
        border-color: hsl(var(--warning) / .2);
    }
    &--info {
        background-color: hsl(var(--info) / .1) !important; 
        color: hsl(var(--info)) !important;
        border-color: hsl(var(--info) / .2);
    }
    &--dark {
        background-color: hsl(var(--dark) / .1) !important; 
        color: hsl(var(--dark)) !important;
        border-color: hsl(var(--dark) / .2);
    }
}
/* ================================= Badge Css End =========================== */