/* ================================= Border Color Css Start =========================== */
.border {
    &--base {
        border-color: hsl(var(--base)) !important;
    }
    &--base-subtle {
        border-color: hsl(var(--base) / .2) !important;
    }
    &--primary {
        border-color: hsl(var(--primary)) !important;
    }
    &--primary-subtle {
        border-color: hsl(var(--primary) / .2) !important;
    }
    &--secondary {
        border-color: hsl(var(--secondary)) !important;
    }
    &--secondary-subtle {
        border-color: hsl(var(--secondary) / .2) !important;
    }
    &--success {
        border-color: hsl(var(--success)) !important;
    }
    &--success-subtle {
        border-color: hsl(var(--success) / .2) !important;
    }
    &--danger {
        border-color: hsl(var(--danger)) !important;
    }
    &--danger-subtle {
        border-color: hsl(var(--danger) / .2) !important;
    }
    &--warning {
        border-color: hsl(var(--warning)) !important;
    }
    &--warning-subtle {
        border-color: hsl(var(--warning) / .2) !important;
    }
    &--info {
        border-color: hsl(var(--info)) !important;
    }
    &--info-subtle {
        border-color: hsl(var(--info) / .2) !important;
    }
    &--dark {
        border-color: hsl(var(--dark)) !important;
    }
    &--dark-subtle {
        border-color: hsl(var(--dark) / .2) !important;
    }
}
/* ================================= Border Color Css End =========================== */