.process {
     position: relative;
     z-index: 2;
     &__vector-1 {
          position: absolute;
          bottom: 10%;
          right: 50px;
          width: 150px;
          filter: drop-shadow(0px 40px 25px hsl(var(--black) / .4));
          animation: pulse 3s linear infinite;
          z-index: -1;
          opacity: 0.7;
          @include lg-screen {
               bottom: 80px;
               right: 30px;
               width: 65px;
               filter: drop-shadow(0px 20px 25px hsl(var(--black) / .4));
          }
          @include md-screen {
               bottom: 30px;
               right: 20px;
               width: 30px;
               filter: drop-shadow(0px 10px 10px hsl(var(--black) / .4));
          }
     }
     &__card {
          height: 100%;
          background: linear-gradient(45deg, hsl(var(--base) / .05), hsl(var(--base-two) / .05), hsl(var(--base) / .05));
          border-radius: 10px;
          position: relative;
          &__content {
               padding: 30px;
               gap: 20px;
               text-align: center;
               @include lg-screen {
                    padding: 30px 20px;
               }
          }
          &__number {
               display: block;
               width: 80px;
               height: 80px;
               background: hsl(var(--base));
               border-radius: 50%;
               box-shadow: 0px 0px 30px 0px hsl(var(--base) / .3);
               color: hsl(var(--white));
               display: flex;
               justify-content: center;
               align-items: center;
               font-family: var(--heading-font);
               @include fs-50;
               line-height: 1;
               font-weight: 700;
               margin: -70px auto 25px;
               position: relative;
               z-index: 2;
               @include lg-screen {
                    width: 70px;
                    height: 70px;
                    @include fs-40;
                    margin-top: -65px;
                    margin-bottom: 20px;
               }
               @include md-screen {
                    width: 60px;
                    height: 60px;
                    @include fs-40;
                    margin-top: -60px;
                    margin-bottom: 20px;
               }
          }
          &__title {
               @include fs-30;
               line-height: 1;
               font-weight: 600;
               margin-bottom: 10px;
               @include lg-screen {
                    @include fs-25
               }
               @include md-screen {
                    @include fs-25;
               }
          }
          &__desc {
               @include fs-16;
               line-height: 1.6;
               @include lg-screen {
                    @include fs-15
               }
               @include md-screen {
                    @include fs-14;
               }
          }
          &__img {
               padding: 0 0 70px;
               display: flex;
               justify-content: center;
               align-items: center;
               position: relative;
               overflow: hidden;
               z-index: 2;
               &::after {
                    content: "";
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    width: 100%;
                    aspect-ratio: 1 / 1;
                    background: hsl(var(--white));
                    border-bottom: 1px solid hsl(var(--base) / .1);
                    border-radius: 0 0 50% 50%;
                    z-index: -1;
               }
               img {
                    max-width: 200px;
               }
          }
     }
}