/* ================================= Form Css Start =========================== */
/* Form Label */
.form--label {
    width: max-content;
    max-width: 100%;
    display: block;
    margin-top: -5px;
    margin-bottom: 6px;
    @extend .fs-15;
    color: hsl(var(--black) / .7);
    font-weight: 600;
}

.form-group {
    margin-bottom: 1rem;
}
/* Form Select */   
select {
    color: hsl(var(--black) / .5) !important;
    &:focus {
        color: hsl(var(--black) / .8) !important;
    }
    option {
        color: hsl(var(--black) / .8);
    }
}
/* Form Select End */

/* Form Control Start */
.form--control {
    font-weight: 400;
    outline: none;
    width: 100%;
    padding: 9px 15px;
    background-color: transparent !important;
    border: 1px solid hsl(var(--black) / .1);
    border-radius: 5px;
    color: hsl(var(--black) / .8);
    line-height: 1.8;
    transition: .3s;
    @include lg-screen {
        padding: 6px 15px;
    }
    @include sm-screen {
        padding: 4px 15px;
    }
    &::placeholder {
        color: hsl(var(--black) / .5);
    }
    &:focus {
        border-color: hsl(var(--base-d-100)); 
        box-shadow: none;
    }
    &:disabled, &[readonly] {
        background-color: hsl(var(--black) / .03) !important; 
        opacity: 1;
    }
    &[type="password"] {
        color: hsl(var(--black)/.5);
        &:focus {
            color: hsl(var(--black)); 
        }
    }
    &[type="file"] {
        line-height: 32px;
        padding: 0;
        position: relative;
        &::file-selector-button {
            border: 1px solid hsl(var(--black) / .08);
            padding: 4px 15px;
            background-color: hsl(var(--base)) !important;
            transition: .2s linear;
            line-height: 32.6px;
            position: relative;
            color: hsl(var(--white)) !important;
            &:hover {
                background-color: hsl(var(--base));
                border: 1px solid hsl(var(--base));
                color: hsl(var(--black));
            }
        }
    }
}
/* Form Control End */

// Message Field Css
textarea.form--control {
    height: 130px;
}
/* Autofill Css */
input:-webkit-autofill, input:-webkit-autofill:hover, input:-webkit-autofill:focus, input:-webkit-autofill:active, 
textarea:-webkit-autofill, textarea:-webkit-autofill:hover, textarea:-webkit-autofill:focus, textarea:-webkit-autofill:active {
    -webkit-transition: background-color 5000s ease-in-out 0s;
    transition: background-color 5000s ease-in-out 0s;
}
input:-webkit-autofill, textarea:-webkit-autofill, select:-webkit-autofill, textarea:-webkit-autofill, textarea:-webkit-autofill, textarea:-webkit-autofill {
    -webkit-box-shadow: 0 0 0px 1000px transparent inset;
    -webkit-text-fill-color: hsl(var(--black)) !important;
    caret-color: hsl(var(--black));
}
/* Autofill Css End */

/* input group */
.input--group {
    position: relative;
    display: flex;
    border: 1px solid hsl(var(--black) / .1);
    border-radius: 5px;
    overflow: hidden;
    transition: .3s;
    .input-group-text {
        background-color: hsl(var(--black) / .05);
        border: 0;
        border-radius: 0;
        color: hsl(var(--black));
        @include md-screen {
            @include fs-14;
            padding-left: 10px;
            padding-right: 10px;
        }
    }
    .form--control {
        border: 0;
    }
    .btn {
        border: 0;
    }
    &:focus-within {
        border-color: hsl(var(--base));
    }
}
/* Show Hide Password */
input#your-password, input#confirm-password {
    padding-right: 50px;
}
.password-show-hide {
    position: absolute;
    right: 20px;
    z-index: 5;
    cursor: pointer;
    top: 50%;
    transform: translateY(-50%);
    color: hsl(var(--black)/.4);
}
/* --------------- Number Arrow None --------------------- */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}
input[type=number]{
    -moz-appearance: textfield;
}

/* Custom Checkbox Design */
.form--check {
    @extend .flex-wrap; 
    a {
        display: inline;
    }
    .form-check-input {
        box-shadow: none;
        background-color: transparent;
        box-shadow: none !important;
        border: 0;
        position: relative;
        width: 16px;
        height: 16px;
        border: 1px solid hsl(var(--black)/.2);
        cursor: pointer;
        &:checked {
            background-color: hsl(var(--base)) !important;
            border-color: hsl(var(--base)) !important;
            box-shadow: none;
            &[type=checkbox] {
                background-image:none;
            }
            &::before {
                position: absolute;
                content: "\f00c";
                font-family: "Font Awesome 5 Free";
                font-weight: 900;
                color: hsl(var(--white)); 
                @extend .fs-11; 
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }
        }
    }
    .form-check-label {
        user-select: none;
        font-weight: 600;
        width: calc(100% - 16px);
        padding-left: 5px;
        cursor: pointer;
        @extend .fs-15;
    }
    label {
        @include xsm-screen {
            @include fs-15; 
        }
    }
    a {
        @include xsm-screen {
            @include fs-15; 
        }
    }
}
/* Custom Radio Design */
.form--radio {
    @extend .flex-wrap; 
    .form-check-input {
        box-shadow: none;
        border: 1px solid hsl(var(--black)/.2);
        position: relative;
        background-color: transparent;
        cursor: pointer;
        width: 16px;
        height: 16px;
        &:active {
            filter: brightness(100%);
        }
        &:checked {
            background-color: transparent;
            border-color: hsl(var(--base));
            &[type=radio] {
                background-image:none ;
            }
            &::before {
                position: absolute;
                content: "";
                top: 50%;
                left: 50%;
                -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
                width: 7px;
                height: 7px;
                background-color: hsl(var(--base));
                border-radius: 50%;
                z-index: 999;
            }
        } 
    }
    .form-check-label {
        user-select: none;
        font-weight: 600;
        width: calc(100% - 16px);
        padding-left: 5px;
        cursor: pointer;
        @extend .fs-15;
    }
}
/*  Custom Switch Design */
.form--switch {
    .form-check-input {
        background-image: none;
        position: relative;
        box-shadow: none;
        border: 0;
        background-color: hsl(var(--black)/.2) !important;
        padding: 10px !important;
        margin-left: 0;
        margin-bottom: 5px;
        width: 70px;
        height: 38px;
        cursor: pointer;
        &:focus {
            background-image: none;
            position: relative;
            box-shadow: none;
            border: 0;
        }
        &::before {
            position: absolute;
            content: "";
            width: 28px;
            height: 28px;
            background-color: hsl(var(--white));
            top: 50%;
            transform: translateY(-50%);
            left: 5px;
            border-radius: 50%;
            transition: 0.2s linear;
        }
        &:checked {
            background-color: hsl(var(--base)) !important;
            &::before {
                left: calc(100% - 33px);
                background-color: hsl(var(--white)) !important;
            }
        }
        &:checked[type=checkbox] {
            background-image: none;
        }
    }
    .form-check-label {
        width: calc(100% - 14px);
        padding-left: 5px;
        cursor: pointer;
    }
}
/*  Custom Switch End Design */



.select2-container {
    display: block;
    height: 45px;
}
.select2-container .selection {
    display: block;
    width: 100%;
}
.select2-container--default .select2-selection--single {
    display: block;
    width: 100%;
    height: 45px;
    border: 1px solid hsl(var(--black) / .1);
}
.select2-container--default .select2-selection--single:focus {
    border-color: hsl(var(--base));
}
.select2-container .select2-selection--single .select2-selection__rendered {
    padding: 0;
    display: flex;
}
.select2-container .select2-selection--single .select2-selection__rendered span {
    display: flex;
    align-items: center;
}
.select2-container--default .select2-selection--single .select2-selection__arrow {
    display: none;
}
.select2-container--default .select2-selection--single .select2-selection__placeholder img {
    display: none;
}
.select2-container--open .select2-dropdown {
    min-width: max-content !important;
    border: 1px solid hsl(var(--black) / .1);
    overflow: hidden;
    @include fs-14;
}
.select2-container--default .select2-search--dropdown .select2-search__field {
    border: 1px solid hsl(var(--black) / .1);
    box-shadow: 0 0;
    padding: 4px 15px;
}
.select2-container--default .select2-search--dropdown .select2-search__field:focus {
    border-color: hsl(var(--base));
}
.select2-container--default .select2-results>.select2-results__options {
    &::-webkit-scrollbar {
		width: 3px;
		height: 10px;
	}
	&::-webkit-scrollbar-thumb {
		background-color: #a8a8a85e;
	}
	&::-webkit-scrollbar-track {
		background-color: #F1F1F1;
	}
}
.select2-container--default .select2-results__option[aria-selected=true] {
    background-color: hsl(var(--black) / .05);
}
.select2-container--default .select2-results__option--highlighted[aria-selected] {
    background-color: hsl(var(--base));
}


/* ================================= Form Css End =========================== */