.why-choose {
     position: relative;
     z-index: 2;
     &__vector-1 {
          position: absolute;
          top: 50%;
          left: -100px;
          width: 400px;
          transform: translateY(-50%);
          opacity: 0.2;
          z-index: -1;
          @include lg-screen {
               width: 250px;
          }
          @include md-screen {
               width: 200px;
          }
     }
     &__row {
          >*:nth-child(6n + 2) {
               .why-choose__card__icon {
                    background: hsl(var(--danger) / .15);
                    color: hsl(var(--danger));
               }
          }
          >*:nth-child(6n + 3) {
               .why-choose__card__icon {
                    background: hsl(var(--success) / .15);
                    color: hsl(var(--success));
               }
          }
          >*:nth-child(6n + 4) {
               .why-choose__card__icon {
                    background: hsl(var(--warning) / .15);
                    color: hsl(var(--warning));
               }
          }
          >*:nth-child(6n + 5) {
               .why-choose__card__icon {
                    background: hsl(var(--base-two) / .15);
                    color: hsl(var(--base-two));
               }
          }
          >*:nth-child(6n + 6) {
               .why-choose__card__icon {
                    background: hsl(var(--info) / .15);
                    color: hsl(var(--info));
               }
          }
     }
     &__card {
          background: hsl(var(--white));
          box-shadow: 0px 0px 35px 0px hsl(var(--base) / .05);
          padding: 40px;
          border-radius: 10px;
          position: relative;
          overflow: hidden;
          transition: .3s;
          z-index: 2;
          @include lg-screen {
               padding: 30px;
               max-width: 320px;
               margin: auto;
          }
          &::before {
               content: "";
               position: absolute;
               top: -50px;
               right: -50px;
               width: 180px;
               height: 180px;
               background: hsl(var(--base-two));
               border-radius: 50%;
               opacity: 0.07;
               transition: .3s;
               z-index: -1;
               @include lg-screen {
                    width: 150px;
                    height: 150px;
               }
          }
          &::after {
               content: "";
               position: absolute;
               bottom: 0;
               left: 50px;
               right: 50px;
               height: 4px;
               background: hsl(var(--base));
               border-radius: 3px;
               transition: .3s;
               z-index: -1;
          }
          &__icon {
               width: 70px;
               height: 70px;
               display: flex;
               justify-content: center;
               align-items: center;
               background: hsl(var(--base) / .15);
               color: hsl(var(--base));
               border-radius: 10px;
               @include fs-45;
               margin: 0 auto 25px;
               @include lg-screen {
                    width: 60px;
                    height: 60px;
                    @include fs-35;
               }
          }
          &__txt {
               text-align: center;
          }
          &__title {
               @include fs-30;
               line-height: 1;
               font-weight: 600;
               position: relative;
               padding-bottom: 10px;
               margin-bottom: 25px;
               transition: .3s;
               @include lg-screen {
                    @include fs-27;
                    padding-bottom: 7px;
                    margin-bottom: 20px;
               }
               @include md-screen {
                    @include fs-25;
                    padding-bottom: 5px;
                    margin-bottom: 20px;
               }
               &::after {
                    content: "";
                    position: absolute;
                    bottom: 0;
                    left: 50%;
                    transform: translateX(-50%);
                    width: 50px;
                    height: 2px;
                    background: hsl(var(--black) / .1);
                    border-radius: 1px;
               }
          }
          &:hover {
               &:before {
                    transform: scale(1.2);
               }
               &::after {
                    background: hsl(var(--base-two-l-200));
                    left: 30px;
                    right: 30px;
               }
               .why-choose__card__title {
                    color: hsl(var(--base));
               }
          }
     }
}