.two-fa-setting {
     &__title {
          @include fs-35;
          line-height: 1;
          font-weight: 700;
          color: hsl(var(--black) / .7);
          margin-bottom: 15px;
          @include md-screen {
               @include fs-30;
          }
          @include sm-screen {
               @include fs-25;
          }
     }
     >* {
          margin-bottom: 15px;
          &:last-child {
               margin-bottom: 0;
          }
     }
}
.download-app {
     display: flex;
     flex-wrap: wrap;
     gap: 10px;
     a {
          &:hover {
               transform: translateY(-2px);
               box-shadow: 0px 10px 30px 0px hsl(var(--black) / .1);
          }
     }
}