/* ====================== Breadcrumb Css Start ==================== */ 
.breadcrumb {
    display: block;
    margin-bottom: 25px;
    background: hsl(var(--white));
    padding-bottom: 15px;
    border-bottom: 1px solid hsl(var(--black) / .07);
    position: relative;
    &::after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        width: 80px;
        height: 2px;
        background: hsl(var(--base));
        border-radius: 1px;
    }
    &__title {
        @include fs-25;
        font-weight: 600;
        color: hsl(var(--black) / .7);
        margin-bottom: 0;
    }
    &__desc {
        @include fs-14;
        line-height: 1.5;
        margin-bottom: 0;
    }
}
/* ====================== Breadcrumb Css End ==================== */
