.trade-proposal {
     &__title {
          @include fs-30;
          line-height: 1.2;
          font-weight: 700;
          margin-bottom: 10px;
          @include xsm-screen {
               @include fs-22;
          }
     }
     &__desc {
          margin-bottom: 30px;
     }
     &__user-info {
          display: flex;
          flex-wrap: wrap;
          gap: 30px;
          border-bottom: 1px solid hsl(var(--black) / .1);
          padding-bottom: 15px;
          margin-bottom: 20px;
          &__img {
               width: 100px;
               height: 100px;
               border-radius: 10px;
               overflow: hidden;
          }
          &__content {
               width: calc(100% - 130px);
          }
          &__name {
               @include fs-16;
               line-height: 1;
               font-weight: 600;
               margin-bottom: 10px;
          }
          &__feedback {
               margin-bottom: 15px;
          }
          &__location {
               @include fs-14;
               line-height: 1;
               color: hsl(var(--black) / .6);
          }
          &__verification {
               width: 100%;
               ul {
                    line-height: 2;
               }
          }
     }
     &__trade-info {
          &__group {
               margin-bottom: 30px;
               &:last-child {
                    margin-bottom: 0;
               }
          }
          &__title {
               font-weight: 600;
               color: hsl(var(--black) / .6);
               margin-bottom: 10px;
          }
          &__desc {
               line-height: 1.7;
          }
     }
     &__feedback {
          &__card {
               background: hsl(var(--base) / .02);
               border: 1px solid hsl(var(--base) / .03);
               padding: 20px;
               border-radius: 10px;
               margin-top: 25px;
               display: flex;
               gap: 15px;
          }
          &__user {
               width: 50px;
               height: 50px;
               border-radius: 5px;
               overflow: hidden;
          }
          &__content {
               width: calc(100% - 65px);
               &__top {
                    display: flex;
                    justify-content: space-between;
                    margin-bottom: 15px;
               }
          }
          &__name {
               @include fs-22;
               line-height: 1;
               font-weight: 700;
               color: hsl(var(--black) / .6);
               display: flex;
               gap: 10px;
               margin-bottom: 10px;
               i {
                    @include fs-18;
               }
          }
          &__date {
               font-style: italic;
               display: block;
               @include fs-14;
               line-height: 1;
               font-weight: 600;
               color: hsl(var(--black) / .6);
          }
          &__desc {
               @include fs-16;
               line-height: 1.7;
          }
     }
}