/* =========================== Banner Section Start Here ========================= */
.banner-section {
    padding-top: 91px;
    position: relative;
    z-index: 2;
    @include md-screen {
        padding-top: 61px;
        padding-bottom: 60px;
    }
    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(-100deg, hsl(var(--base) / .07), hsl(var(--base-two) / .07));
        z-index: -1;
    }
    &__background {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-position: right bottom !important;
        z-index: -1;
        @include md-screen {
            background-position-x: 70% !important;
        }
    }
    &__vector-1 {
        position: absolute;
        top: 150px;
        right: 100px;
        width: 80px;
        filter: drop-shadow(0px 40px 25px hsl(var(--black) / .3));
        animation: pulse 3s linear infinite;
        opacity: 0.7;
        z-index: -1;
        @include lg-screen {
            top: 120px;
            right: 30px;
            width: 60px;
            filter: drop-shadow(0px 20px 25px hsl(var(--black) / .3));
        }
        @include md-screen {
            top: 70px;
            right: 10px;
            width: 40px;
            filter: drop-shadow(0px 10px 10px hsl(var(--black) / .4));
        }
    }
    &__vector-2 {
        position: absolute;
        bottom: 0;
        left: 150px;
        width: 60px;
        filter: drop-shadow(0px 40px 25px hsl(var(--black) / .3));
        animation: pulse 3s linear infinite;
        opacity: 0.7;
        z-index: -1;
        @include lg-screen {
            bottom: 20px;
            left: 70px;
            width: 40px;
            filter: drop-shadow(0px 15px 25px hsl(var(--black) / .4));
        }
        @include md-screen {
            width: 25px;
            bottom: 0;
            left: 20px;
            filter: drop-shadow(0px 10px 10px hsl(var(--black) / .4));
        }
    }
    &__vector-3 {
        position: absolute;
        top: 120px;
        left: 25%;
        width: 70px;
        filter: drop-shadow(0px 40px 25px hsl(var(--black) / .3));
        animation: pulse 3s linear infinite;
        opacity: 0.7;
        z-index: -1;
        @include lg-screen {
            top: 120px;
            left: 10%;
            width: 50px;
            filter: drop-shadow(0px 20px 15px hsl(var(--black) / .3));
        }
        @include md-screen {
            top: 80px;
            left: 10%;
            width: 30px;
            filter: drop-shadow(0px 10px 10px hsl(var(--black) / .4));
        }
    }
}
@keyframes pulse {
    0% {
        transform: scale(0.9);
    }
    50% {
        transform: scale(1);
    }
    100% {
        transform: scale(0.9);
    }
}
.banner-content {
    padding: 180px 0;
    overflow: hidden;
    @include md-screen {
        padding: 60px 0;
    }
    &__subtitle {
        font-family: var(--body-font);
        width: max-content;
        color: hsl(var(--white));
        background-image: linear-gradient(to left, hsl(var(--base-d-100)), hsl(var(--base-two-d-100)));
        @include fs-16;
        border-radius: 3px;
        padding: 5px 20px;
        font-weight: 500;
        margin-bottom: 25px;
        @include md-screen {
            @include fs-16;
            padding: 3px 15px;
            margin-bottom: 20px;
        }
    }
    &__title {
        @include fs-60;
        line-height: 1.1;
        font-weight: 700;
        color: hsl(var(--black));
        margin-bottom: 20px;
        @include lg-screen {
            @include fs-50;
        }
        @include md-screen {
            @include fs-35;
            line-height: 1.2;
            margin-bottom: 15px;
        }
    }
    &__desc {
        @include fs-18;
        line-height: 1.7;
        color: hsl(var(--black) / .8);
        @include md-screen {
            @include fs-16;
        }
    }
}
.banner-calculator {
    background: hsl(var(--white));
    padding: 30px;
    border-radius: 10px;
    box-shadow: 0px 0px 35px 0px hsl(var(--black) / .05);
    @include lg-screen {
        padding: 25px;
    }
    @include md-screen {
        padding: 20px;
    }
    &__title {
        text-align: center;
        @include fs-30;
        line-height: 1;
        font-weight: 700;
        border-bottom: 1px solid hsl(var(--black) / .1);
        padding-bottom: 15px;
        margin-bottom: 20px;
        @include md-screen {
            @include fs-25;
            padding-bottom: 10px;
        }
    }
}
/* =========================== Banner Section End Here ========================= */