/* ======================= Header Start Here ======================= */
.header {
    position: fixed;
    top: 0;
    width: 100%;
    height: 70px;
    background: hsl(var(--white));
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    border-bottom: 1px solid hsl(var(--black) / .05);
    z-index: 11;
    &__right {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        gap: 20px;
        .form {
            position: relative;
            transition: .3s;
            input {
                width: 300px;
                height: 40px;
                border: 1px solid hsl(var(--black) / .1);
                border-radius: 20px;
                outline: none;
                padding: 0 20px;
                background: transparent;
                &:focus {
                    border-color: hsl(var(--base));
                }
            }
            ul {
                position: absolute;
                top: 100%;
                left: 0;
                width: 100%;
                background: hsl(var(--white));
                border: 1px solid hsl(var(--black) / .1);
                border-radius: 10px;
                padding: 10px;
                max-height: 400px;
                overflow: auto;
                li {
                    padding: 10px;
                    border-bottom: 1px solid hsl(var(--black) / .05);
                    border-radius: 5px;
                    transition: .3s;
                    a {
                        display: -webkit-box;
                        text-overflow: ellipsis;
                        -webkit-line-clamp: 2;
                        -webkit-box-orient: vertical;
                        overflow: hidden;
                    }
                    &:hover {
                        background: hsl(var(--base-l-900));
                        border-color: transparent;
                    }
                }
            }
        }
    }
}
/* ======================= Header Css End ======================= */